import React from 'react'
import { Route, Routes } from 'react-router-dom';
import { useSelector } from 'react-redux';
import SignUp from './Pages/SignUp';
import Login from './Pages/Login';
import Home from './Pages/Home';
import ProductPage from './Pages/ProductPage';
import Dashboard from './Pages/Dashboard';

const App = () => {

  const userData=useSelector(state=>state.userReducer);
  

  return (
    <div className='App'>

      <Routes>
        {/* {userData.isLoggedIn ? <Route path='/home' element={<Home/>}/> : <Route path='/home' element={<Login/>}/>} */}
        {userData.isLoggedIn ? <Route path='/logout' element={<Home/>}/> : <Route path='/home' element={<Login/>}/>}
        <Route path='/' element={<Home/>}/>
        <Route path='/home' element={<Home/>}/>
        <Route path='/login' element={<Login/>}/>
        <Route path='/signup' element={<SignUp/>}/>
        <Route path='/dashboard' element={<Dashboard/>}/>
        <Route path="/products/:supplierId" element={<ProductPage />} />

      </Routes>
      
    </div>
  )
}

export default App