import React, { useEffect, useState } from "react";
import axios from "../utils/axios";
import {
  BarChart,
  Bar,
  XAxis,
  YAxis,
  Tooltip,
  CartesianGrid,
  PieChart,
  Pie,
  Cell,
} from "recharts";
import styles from "./Dashboard.module.css";
import { useDispatch, useSelector } from "react-redux";

const Dashboard = () => {
  const dispatch = useDispatch();
  const userData = useSelector((state) => state.userReducer);

  const [supplier, setSupplier] = useState(null);
  const [products, setProducts] = useState([]);
  const [newProduct, setNewProduct] = useState({
    name: "",
    category: "",
    stock: "",
    supplierId: "",
  });
  const [suggestions, setSuggestions] = useState("");
  const [loading, setLoading] = useState(false);
  const [showModal, setShowModal] = useState(false);
  const [supplierForm, setSupplierForm] = useState({
    name: "",
    email: "",
    phone: "",
    address: "",
    owner: "",
    gst: "",
    category: "",
  });

  const userId = userData.email;
  const supplierId = userData.supplierId;
  console.log("Supplier ID:", supplierId);

  //  Fetch supplier and suggestions
  useEffect(() => {
    if (supplierId) {
      const loadData = async () => {
        try {
          await fetchSupplierData();
        } catch (err) {
          console.error("Error fetching supplier data", err);
        } finally {
          // Fetch suggestions after supplier is loaded
          setTimeout(() => {
            fetchSuggestions();
          }, 700);
        }
      };
      loadData();
    }
  }, [supplierId]);

  const fetchSupplierData = async () => {
    try {
      const { data } = await axios.get(`user/productList/${supplierId}`);
      console.log("Supplier Data:", data.supplier);
      setSupplier(data.supplier);
      setProducts(data.supplier.products);
    } catch (err) {
      console.error("Error fetching supplier data", err);
    }
  };

  const fetchSuggestions = async () => {
    try {
      const { data } = await axios.post(`supplier/suggestion`, { supplierId });
      console.log("Fetched Suggestion:", data.suggestion);
      setSuggestions(data.suggestion || "");
    } catch (err) {
      console.error("Error fetching suggestions", err);
    }
  };

  const handleAddProduct = async (e) => {
    e.preventDefault();
    try {
      setLoading(true);
      newProduct.supplierId = supplierId;
      await axios.post(`supplier/addProduct`, newProduct);
      setNewProduct({ name: "", category: "", stock: "" });
      fetchSupplierData();
    } catch (err) {
      console.error("Error adding product", err);
    } finally {
      setLoading(false);
    }
  };

  const handleUpdate = async (index, newStock) => {
    try {
      const updated = [...products];
      updated[index].stock = newStock;
      setProducts(updated);
      await axios.post("supplier/updateProduct", {
        supplierId: supplierId,
        productIndx: index,      // numeric index in supplier.products array
        stock: newStock      // numeric or string that can be Number(...)
    });

    } catch (err) {
      console.error("Error updating product", err);
    }
  };

  const handleRegisterSupplier = async (e) => {
    e.preventDefault();
    try {
      setLoading(true);
      const { data } = await axios.post("supplier/register", {
        userId,
        ...supplierForm,
      });
      console.log(data);
      dispatch({ type: "SET_USER", payload: data.Supplier });
      alert("Supplier Registered Successfully!");
      setShowModal(false);
      window.location.reload();
    } catch (err) {
      alert(err.response?.data?.message || "Error registering supplier");
    } finally {
      setLoading(false);
    }
  };

  const stockData = products.map((p) => ({ name: p.name, stock: p.stock }));
  const feedbackData =
    supplier?.feedback?.map((f, i) => ({
      name: `Feedback ${i + 1}`,
      rating: f.rating,
    })) || [];
  const COLORS = ["#82ca9d", "#8884d8", "#ffc658", "#ff8042"];

  //  No supplier registered yet
  if (!supplierId) {
    return (
      <div className={styles.noSupplier}>
        <h2>Welcome to Supplier Dashboard</h2>
        <p>It looks like you havent registered your business yet.</p>
        <button
          className={styles.addBusinessBtn}
          onClick={() => setShowModal(true)}
        >
           Add Supplier Business
        </button>

        {/*  Supplier Registration Popup */}
        {showModal && (
          <div className={styles.modalOverlay}>
            <div className={styles.modalContent}>
              <h3>Register Supplier</h3>
              <form
                onSubmit={handleRegisterSupplier}
                className={styles.modalForm}
              >
                <input
                  type="text"
                  placeholder="Business Name"
                  value={supplierForm.name}
                  onChange={(e) =>
                    setSupplierForm({ ...supplierForm, name: e.target.value })
                  }
                  required
                />
                <input
                  type="email"
                  placeholder="Email"
                  value={supplierForm.email}
                  onChange={(e) =>
                    setSupplierForm({ ...supplierForm, email: e.target.value })
                  }
                  required
                />
                <input
                  type="text"
                  placeholder="Phone Number"
                  value={supplierForm.phone}
                  onChange={(e) =>
                    setSupplierForm({ ...supplierForm, phone: e.target.value })
                  }
                  required
                />
                <input
                  type="text"
                  placeholder="Owner Name"
                  value={supplierForm.owner}
                  onChange={(e) =>
                    setSupplierForm({ ...supplierForm, owner: e.target.value })
                  }
                  required
                />
                <input
                  type="text"
                  placeholder="GST Number"
                  value={supplierForm.gst}
                  onChange={(e) =>
                    setSupplierForm({ ...supplierForm, gst: e.target.value })
                  }
                  required
                />
                <input
                  type="text"
                  placeholder="Business Category"
                  value={supplierForm.category}
                  onChange={(e) =>
                    setSupplierForm({
                      ...supplierForm,
                      category: e.target.value,
                    })
                  }
                  required
                />
                <textarea
                  placeholder="Address"
                  value={supplierForm.address}
                  onChange={(e) =>
                    setSupplierForm({
                      ...supplierForm,
                      address: e.target.value,
                    })
                  }
                  required
                ></textarea>

                <div className={styles.modalButtons}>
                  <button type="submit" disabled={loading}>
                    {loading ? "Registering..." : "Register"}
                  </button>
                  <button
                    type="button"
                    className={styles.cancelBtn}
                    onClick={() => setShowModal(false)}
                  >
                    Cancel
                  </button>
                </div>
              </form>
            </div>
          </div>
        )}
      </div>
    );
  }

  if (!supplier)
    return <p className={styles.loading}>Loading supplier details...</p>;

  return (
    <div className={styles.dashboard}>
      <h2>Supplier Dashboard</h2>

      {/* Supplier Info */}
      <div className={styles.infoBox}>
        <p><b>Name:</b> {supplier.name}</p>
        <p><b>Email:</b> {supplier.email}</p>
        <p><b>Phone:</b> {supplier.phone}</p>
        <p><b>Address:</b> {supplier.address}</p>
        <p><b>GST:</b> {supplier.gst}</p>
      </div>

      {/* Add Product */}
      <div className={styles.section}>
        <h3>Add Product</h3>
        <form className={styles.addForm} onSubmit={handleAddProduct}>
          <input
            type="text"
            placeholder="Product Name"
            value={newProduct.name}
            onChange={(e) =>
              setNewProduct({ ...newProduct, name: e.target.value })
            }
            required
          />
          <input
            type="text"
            placeholder="Category"
            value={newProduct.category}
            onChange={(e) =>
              setNewProduct({ ...newProduct, category: e.target.value })
            }
            required
          />
          <input
            type="number"
            placeholder="Stock"
            value={newProduct.stock}
            onChange={(e) =>
              setNewProduct({ ...newProduct, stock: e.target.value })
            }
            required
          />
          <button type="submit" disabled={loading}>
            {loading ? "Adding..." : "Add Product"}
          </button>
        </form>
      </div>

      {/* Product List */}
      <div className={styles.section}>
        <h3>Product List</h3>
        {products.length ? (
          <table className={styles.table}>
            <thead>
              <tr>
                <th>Name</th>
                <th>Category</th>
                <th>Stock</th>
                <th>Update</th>
              </tr>
            </thead>
            <tbody>
              {products.map((p, index) => (
                <tr key={index}>
                  <td>{p.name}</td>
                  <td>{p.category}</td>
                  <td>
                    <input
                      type="number"
                      value={p.stock}
                      onChange={(e) => handleUpdate(index, e.target.value)}
                      className={styles.stockInput}
                    />
                  </td>
                  <td>
                    <button onClick={() => handleUpdate(index, p.stock)}>
                      Save
                    </button>
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        ) : (
          <p>No products yet.</p>
        )}
      </div>

      {/*  Suggestions */}
      <div className={styles.section}>
        <h3>AI Suggestions</h3>
        {loading ? (
          <p>Loading suggestions...</p>
        ) : suggestions && suggestions.trim() !== "" ? (
          <div
            className={styles.suggestionBox}
            dangerouslySetInnerHTML={{
              __html: suggestions
                .replace(/\*\*(.*?)\*\*/g, "<b>$1</b>")
                .replace(/\n/g, "<br>"),
            }}
          ></div>
        ) : (
          <p>No suggestions yet.</p>
        )}
      </div>

      {/*  Charts */}
      <div className={styles.section}>
        <h3>Performance Charts</h3>
        <div className={styles.chartContainer}>
          <BarChart width={400} height={250} data={stockData}>
            <CartesianGrid strokeDasharray="3 3" />
            <XAxis dataKey="name" />
            <YAxis />
            <Tooltip />
            <Bar dataKey="stock" fill="#82ca9d" />
          </BarChart>

          <PieChart width={400} height={250}>
            <Pie
              data={feedbackData}
              dataKey="rating"
              nameKey="name"
              cx="50%"
              cy="50%"
              outerRadius={80}
              fill="#8884d8"
              label
            >
              {feedbackData.map((entry, index) => (
                <Cell
                  key={`cell-${index}`}
                  fill={COLORS[index % COLORS.length]}
                />
              ))}
            </Pie>
            <Tooltip />
          </PieChart>
        </div>
      </div>
    </div>
  );
};

export default Dashboard;
