const initialState= {
    name:'',
    email:'',
    phone:'',
    supplierId:'',
    isLoggedIn:false
}

function userReducer(state=initialState, action){
    
    switch(action.type){
        
        case 'SET_USER':
            return{
                ...state,
                name:action.payload.name,
                email:action.payload.email,
                phone:action.payload.phone,
                supplierId:action.payload.supplierId,
                isLoggedIn:true
            }
        case 'GET_USER':
            return state
        case 'RESET_USER':
            return initialState
        default:
            return state
    }
    
}

export default userReducer;