import {useSelector} from 'react-redux';
import React, { useState } from 'react'
import {NavLink, useNavigate} from 'react-router-dom';
import Styles from './NavBar.module.css'
import axios from "../utils/axios"
import { useDispatch } from 'react-redux';
import { persistStore } from 'redux-persist';
import store from '../Redux/Store/store'



const NavBar = () => {
    const userData = useSelector(state=>state.userReducer);

    const [display, setDisplay] = useState(false);

     const [showMenu, setShowMenu] = useState(false);

    const dispatch=useDispatch();
    const navigate=useNavigate();
    const persistor = persistStore(store);


    const headClickHandler=()=>{

      (userData.isLoggedIn) ? navigate('/dashboard') : navigate('/');

    }

    const logoutHandler=async()=>{

      let email=userData.email;
      const{data}=await axios.post('auth/logout',{email});
      if(data.success){
          dispatch({type:'RESET_USER'});
          await persistor.purge();
          navigate('/login');
      }
      

    }
    
  return (
    <div className={Styles['navbar']} >
        <img src='logo.png' width={'5%'} height={'150%'}></img>
        <button onClick={headClickHandler} className={Styles['navHead']}>TruSource</button>

         <div className={`${Styles['navLinks']} ${showMenu && !userData.isLoggedIn ? Styles['show'] : ''}`}>

          {!userData.isLoggedIn && (
            <NavLink className={Styles['navItems']} to={'/'} >
              Home
            </NavLink>
          )}

          {!userData.isLoggedIn && (
            <NavLink className={Styles['navItems']} to={'/signup'} >
              SignUp
            </NavLink>
          )}

          {!userData.isLoggedIn && (
            <NavLink className={Styles['navItems']} to={'/login'} >
              Login
            </NavLink>
          )}

          {userData.isLoggedIn && (
            <NavLink className={Styles['navItems']} to={'/login'} >
               <button onClick={headClickHandler} className={Styles['navItems']}>Logout</button>
            </NavLink>
          )}
          
        </div>
          

    </div>
  )
}

export default NavBar
